import { createElement } from '../render.js';
import { POINT_OFFERS } from '../constants.js';
import { getLabel } from '../utils.js';

function createPointOffersTemplate(offers) {
  if (!offers || !offers.length) return '';

  return `
    <h4 class='visually-hidden'>Offers:</h4>
    <ul class='event__selected-offers'>
      ${offers
        .map(
          (offer) => `
        <li class='event__offer'>
          <span class='event__offer-title'>${offer.title}</span>
          &plus;&euro;&nbsp;
          <span class='event__offer-price'>${offer.price}</span>
        </li>
      `,
        )
        .join('')}
    </ul>
  `;
}

function createFavoriteButtonTemplate(isFavorite) {
  const activeClass = isFavorite ? 'event__favorite-btn--active' : '';

  return `
    <button class='event__favorite-btn ${activeClass}' type='button'>
      <span class='visually-hidden'>Add to favorite</span>
      <svg class='event__favorite-icon' width='28' height='28' viewBox='0 0 28 28'>
        <path d='M14 21l-8.22899 4.3262 1.57159-9.1631L.685209 9.67376 9.8855 8.33688 14 0l4.1145 8.33688 9.2003 1.33688-6.6574 6.48934 1.5716 9.1631L14 21z'/>
      </svg>
    </button>
  `;
}

function formatDuration(start, end) {
  const startDate = new Date(start);
  const endDate = new Date(end);
  const diffMs = endDate - startDate;
  const diffMins = Math.floor(diffMs / 60000);

  if (diffMins < 60) {
    return `${diffMins}M`;
  }

  const hours = Math.floor(diffMins / 60);
  const minutes = diffMins % 60;

  if (minutes === 0) {
    return `${hours}H`;
  }

  return `${hours}H ${minutes}M`;
}

function formatDate(dateStr) {
  const date = new Date(dateStr);
  return date
    .toLocaleDateString('en-US', { month: 'short', day: 'numeric' })
    .toUpperCase();
}

function formatTime(dateStr) {
  const date = new Date(dateStr);
  return date.toLocaleTimeString('en-US', {
    hour: '2-digit',
    minute: '2-digit',
    hour12: false,
  });
}

function createRoutePointTemplate(data = {}) {
  const {
    type = 'taxi',
    destination = 'Amsterdam',
    date = '2019-03-18',
    startTime = '2019-03-18T10:30',
    endTime = '2019-03-18T11:00',
    price = 20,
    isFavorite = false
  } = data;

  const offers = POINT_OFFERS[type] || [];
  const formattedDate = formatDate(date);
  const formattedStartTime = formatTime(startTime);
  const formattedEndTime = formatTime(endTime);
  const duration = formatDuration(startTime, endTime);
  const eventTitle = `${getLabel(type)} ${destination}`;

  return `
    <li class='trip-events__item'>
      <div class='event'>
        <time class='event__date' datetime='${date}'>${formattedDate}</time>
        <div class='event__type'>
          <img class='event__type-icon' width='42' height='42' src='img/icons/${type}.png' alt='Event type icon'>
        </div>
        <h3 class='event__title'>${eventTitle}</h3>
        <div class='event__schedule'>
          <p class='event__time'>
            <time class='event__start-time' datetime='${startTime}'>${formattedStartTime}</time>
            &mdash;
            <time class='event__end-time' datetime='${endTime}'>${formattedEndTime}</time>
          </p>
          <p class='event__duration'>${duration}</p>
        </div>
        <p class='event__price'>
          &euro;&nbsp;<span class='event__price-value'>${price}</span>
        </p>
        ${createPointOffersTemplate(offers)}
        ${createFavoriteButtonTemplate(isFavorite)}
        <button class='event__rollup-btn' type='button'>
          <span class='visually-hidden'>Open event</span>
        </button>
      </div>
    </li>
  `;
}

export default class RoutePointView {
  #element = null;
  #data = {};

  constructor(data = {}) {
    this.#data = data;
  }

  get template() {
    return createRoutePointTemplate(this.#data);
  }

  getElement() {
    if (!this.#element) {
      this.#element = createElement(this.template);
    }

    return this.#element;
  }

  removeElement() {
    this.#element = null;
  }
}
